.food-plan {
  padding: 20px 20px;
}

.food-plan__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-rows: 1fr 1fr;
  gap: 20px;
}

.food-plan__item {
  background-color: @basic-grey;
  padding: 30px 20px 18px 20px;

  &--slim {
    background-image: url("../../img/cat-back.svg");
    background-repeat: no-repeat;
    background-position: 22px 24px;
  }

  &--pro {
    background-image: url("../../img/cat-back-pro.svg");
    background-repeat: no-repeat;
    background-position: 12px 24px;
  }
}

.food-plan__title {
  margin: 0;
  margin-bottom: 30px;
  padding-left: 70px;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}

.food-plan__description {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  margin-bottom: 38px;
  color: #444444;
}

.food-plan__button {
  background-color: transparent;
  color: @black;
  font-size: 16px;
  line-height: 16px;
  padding: 4px 50px 4px 0;
  text-align: left;
  background-image: url("../../img/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: right 10% center;
  display: inline-block;
  position: relative;

  &:hover {
    background-image: url("../../img/arrow-right-big.svg");
    background-position: right center;
    background-color: transparent;
  }

  &:active {
    background-image: url("../../img/arrow-right-big.svg");
    background-position: right center;
    background-color: transparent;
    opacity: 0.3;
  }
}

.food-plan__button::before {
  content: "";
  position: absolute;
  width: 240px;
  height: 1px;
  background-color: #d9d9d9;
  top: 0;
  left: 0;
  transform: translateY(-15px);
}
